@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.AboutMe {
  margin-bottom: 5rem; }
  .AboutMe--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 1rem 0; }
  .AboutMe--subHeading {
    font-weight: 400;
    margin: 1rem 0; }
  .AboutMe--text-secondaryColor {
    color: orangered; }
  .AboutMe--main {
    max-width: 1100px;
    margin: 1rem auto;
    display: grid;
    grid-gap: 0rem 2.5rem;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center; }
  .AboutMe--gridHeading {
    grid-area: gh;
    justify-self: left; }
  .AboutMe--image {
    grid-area: portrait;
    border-radius: 50%;
    border: solid 0.3rem orangered;
    margin: auto;
    width: 225px;
    height: 225px; }
  .AboutMe--bio {
    font-size: 1.2rem;
    grid-area: bio;
    text-align: left; }
  .AboutMe--skillsSection {
    background: #fbfbfb;
    padding: 2rem; }
  .AboutMe--skillsSectionHeading {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 0.3rem; }
  .AboutMe--skills {
    background: #fbfbfb;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap; }
  .AboutMe--skillsHeading {
    font-size: 1.2rem;
    text-align: center;
    background: orangered;
    color: #eee;
    padding: 0.3rem 1.5rem;
    border-radius: 1rem; }
  .AboutMe--skillsBorder {
    border: 0.15rem solid orangered;
    border-radius: 1rem;
    margin: 2rem 0rem 5rem; }

@media screen and (min-width: 0px) {
  .AboutMe--main {
    grid-template-columns: 1fr;
    grid-template-areas: "portrait" "gh" "bio"; } }

@media screen and (min-width: 768px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, minmax(10px, 1fr));
    grid-template-areas: ". gh ." "portrait bio bio"; } }

@media screen and (min-width: 1171px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: ". gh ." "portrait bio bio"; } }
